.shopContainer {
    background-color: #fff;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    margin: 2em;
    margin-bottom: 5em;
    padding: 1em;
}
.shopContainerImg {
    margin: 0.5em;
    
}


@media (max-width: 840px) {
    .shopContainer {
        display: flex;
        flex-direction: column;
    }
   
       
    }